.container {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #212529;
}

.reset-password {
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	background: #F8F9FA;
	border-radius: 1rem;
	padding: 2rem;
	max-width: 380px;
	color: #212529;
}

.reset-password h2 {
	font-weight: 600;
	text-align: center;
}

.reset-password .instruction {
	text-align: center;
	font-size: 0.9rem;
	color: #6c757d;
}

.reset-password .error {
	color: #DC3545;
	text-align: center;
}

.reset-password .success {
	color: #198754;
	text-align: center;
}

.reset-password .form {
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	margin: 0.5rem 0;
}

.reset-password .form .input-group {
	display: flex;
	align-items: center;
	gap: 0.5rem;
}

.reset-password .form .input-group .icon {
	font-size: 18px;
}

.reset-password .form .input-group input {
	border: 0;
	outline: 0;
	font-size: 1rem;
	padding: 0.5rem;
	background: #FFFFFF;
	border: 0.5px solid #FFFFFF;
	border-radius: 0.5rem;
	width: 100%;
}

.reset-password .form button {
	border: 0;
	outline: 0;
	font-size: 1rem;
	padding: 0.5rem;
	color: #212529;
	background: transparent;
	border: 0.5px solid #212529;
	border-radius: 0.5rem;
	margin-top: 0.5rem;
}

.reset-password .form button:focus {
	color: #F1F1F1;
	background: #212529;
	border: 0.5px solid #212529;
}

.reset-password .password-requirements {
	display: flex;
	flex-direction: column;
	gap: 2px;
}

.reset-password .password-requirements h4 {
	margin-bottom: 0.25rem;
}

.reset-password .password-requirements p {
	font-size: 0.9rem;
	display: flex;
	align-items: center;
	color: #198754;
}

.reset-password .password-requirements p.invalid {
	color: #DC3545;
}

.reset-password .password-requirements p .icon {
	font-size: 18px;
}

.reset-password .password-requirements p .icon {
	font-size: 18px;
}
